@use "./../foundation/system" as s;

:root {
  /* COLOR */

  /* <-- primary-color --> */
  --clr-primary-200: rgba(255, 255, 255, 0.85); /* <-- Header */
  --clr-primary-400: #0568bf; /* <-- 400 set as standard */
  --clr-primary-500: #0567bd; /* <-- Big circle bottom & hero heading bar*/
  --clr-primary-600: #014fa1; /* <-- xsm icon arrow */
  --clr-primary-700: #003d7c; /* <-- Contact col sub-title */
  --clr-primary-800: rgba(1, 46, 93, 0.9); /* <-- Contact col */

  /* <-- primary-grandient-color --> */

  --clr-gradient-400: linear-gradient(#014fa1, #0568bf);
  --clr-gradient-500: linear-gradient(#014fa1, #0462b8, #0568bf);
  --clr-gradient-600: linear-gradient(#012142, #0568bf);
  --clr-gradient-800: linear-gradient(#030303, #003d7c);

  /* <-- neutral-color --> */
  --clr-neutral-100: #fff; /* Light color */
  --clr-neutral-200: #f2f4f7; /* <-- Form bg */
  --clr-neutral-300: #f3f3f3; /* <-- Hero heading bar*/
  --clr-neutral-600: #333; /* <-- Primary body text */
  --clr-neutral-900: #000; /* Dark color */

  /* FONT FAMILY */
  --ff-primary: "Gothic A1", sant-serif; /* <-- 400, 600, 700 | Yu Gothic Not yet downloaded*/
  --ff-secondary: "Oswald", sant-serif; /* <-- 600, 700*/
  --ff-tertiary: "Inter", sant-serif; /* <-- 700 */
  --ff-minor: "DIN Condensed", sant-serif; /* <-- Need it?? Not yet downloaded*/

  /* Re-custom properties here*/
  --ff-body: var(--ff-primary);
  --ff-heading: var(--ff-secondary);

  /* FONT WEIGHT */
  --fw-regular: 400;
  --fw-semi-bold: 600;
  --fw-bold: 700;

  /* FONT SIZE */
  --fs-400: 1rem; /* Set as a regular size <p>*/
  --fs-500: 1.125rem; /* 18px <h5>*/
  --fs-550: 1.25rem; /* 20px <h4>*/
  --fs-600: 1.5rem; /* 24px <h3>*/
  --fs-700: 2rem; /* 32px <h2>*/
  --fs-750: 2.25rem; /* 36px */
  --fs-800: 4rem; /* 64px Numeric bullet point*/
  --fs-850: 6rem; /* 96px Hero*/
  --fs-900: 6.25rem; /* 100px <h1>*/

  /* Re-custom properties here*/
  --fs-primary-heading: var(--fs-900);
  --fs-secondary-heading: var(--fs-700);
  --fs-body: var(--fs-400);
  --fs-nav: var(--fs-500);
  --fs-button: var(--fs-500);

  /* SIZE */
  --size-100: 0.25rem; /* 4px */
  --size-200: 0.5rem; /* 8px */
  --size-300: 0.75rem; /* 12px */
  --size-400: 1rem; /* 16px */
  --size-450: 1.5rem; /* 24px */
  --size-500: 1.75rem; /* 28px */
  --size-650: 2rem; /* 32px */
  --size-600: 3rem; /* 48px */
  --size-700: 4rem; /* 64px */
  --size-800: 6rem; /* 96px */
  --size-850: 7.5rem; /* 120px */
  --size-900: 10rem; /* 160px */
}

/* Container */
section {
  padding: 6rem 2rem;
  margin-inline: auto;
}

// hero section
.hero {
  align-items: end;
  display: flex;
  height: 80vh;
  justify-content: space-between;
  position: relative;
  width: 100%;
}

.hero .hero-content {
  color: var(--clr-neutral-100);
  margin-left: 6.63rem;
  margin-bottom: rem;
}

.hero .hero-content .company-name {
  font-size: var(--fs-850);
  font-family: var(--ff-secondary);
  line-height: var(--size-850);
  letter-spacing: 0.6rem;
  margin-bottom: var(--size-500);
}

.hero .hero-content .division {
  background-color: var(--clr-primary-400);
  height: 0.2rem;
  margin-bottom: var(--size-500);
  width: 41.875rem;
}

.hero .hero-content .company-slogan {
  font-size: var(--fs-600);
}

.hero .hero-scroll {
  bottom: -20%;
  position: absolute;
  right: 0;
}

.hero .hero-scroll i {
  color: var(--clr-neutral-100);
  font-size: 21.25rem;
}

// services section

.container-services {
  margin-top: 5rem;
  position: relative;
  text-align: center;
}

.container-services::after {
  background: var(--clr-gradient-600);
  content: "";
  position: absolute;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 30% 100%);
  top: 7rem;
  right: 0;
  width: 48rem;
  height: 52rem;

  z-index: -1;
}

.container-services h1 {
  color: var(--clr-primary-400);
  font-family: var(--ff-secondary);
  font-size: var(--fs-primary-heading);
  margin-left: 6.63rem;
  position: relative;
  text-align: left;
}

.container-services h1::before {
  background: var(--clr-primary-400);
  content: " ";
  display: block;
  height: 0.125rem;
  width: 6rem;
  position: absolute;
  top: 55%;
  left: -5%;
}

.container-services h3 {
  color: var(--clr-neutral-600);
  font-size: var(--fs-600);
  font-weight: var(--fw-semi-bold);
  letter-spacing: 0.24rem;
  margin-left: 6.63rem;
  text-align: left;
}

.container-services .box-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: end;
  margin-block: var(--size-700);
  position: relative;
  text-align: left;
}

.container-services .box-wrapper .box {
  background: #014fa1;
  position: relative;
  height: 35.5rem;
  width: 28rem;
}

.container-services .box-wrapper .box .vertical {
  color: var(--clr-neutral-100);
  font-family: var(--ff-secondary);
  font-size: 0.875rem;
  position: absolute;
  top: 0.25rem;
  right: 0;
  writing-mode: vertical-rl;
}

.container-services .box-wrapper .box .vertical::after {
  background: var(--clr-neutral-100);
  content: " ";
  display: block;
  position: absolute;
  top: -5%;
  height: 35.5rem;
  width: 0.0625rem;
}

.container-services .box-wrapper .box .box-content {
  color: var(--clr-neutral-100);
  inset: 3rem 3.56rem;
  position: absolute;
  padding-block: var(--size-800);
}

.container-services .box-wrapper .box .box-content small {
  font-size: 0.75rem;
}

.container-services .box-wrapper .box .box-content h5 {
  font-family: var(--ff-secondary);
  margin-block: var(--size-450);
}

.container-services .box-wrapper .box .box-content h2 {
  font-family: var(--ff-primary);
  font-size: var(--fs-750);
  font-weight: var(--fw-bold);
  letter-spacing: 0.36rem;
  margin-bottom: 3.4rem;
}

.container-services .box-wrapper .box .box-content a {
  color: var(--clr-neutral-100);
  font-size: var(--fs-400);
  position: relative;
}

.container-services .box-wrapper .box .box-content i {
  color: var(--clr-neutral-100);
  cursor: pointer;
  font-size: 5rem;
  position: absolute;
  bottom: -170%;
  left: 120%;
  margin-inline: 1.3rem;
}

.container-services .btn {
  background: var(--clr-gradient-500);
  border: none;
  border-radius: 2.5rem;
  color: var(--clr-neutral-100);
  font-size: var(--fs-500);
  font-family: var(--ff-primary);
  padding: 2rem 4rem;
  margin-inline: auto;
  text-align: center;
  width: 100%;
}

//company / recruit section
.cards-wrapper {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 10rem;
  width: 100%;
}

.cards-wrapper .card {
  background-color: var(--clr-primary-800);
  position: relative;
  height: 35rem;
  width: 50%;
}

.cards-wrapper .card::before {
  background-color: #000;
  content: "";
  clip-path: polygon(0 0, 100% 0, 70% 100%, 0% 100%);
  inset: 0;
  position: absolute;
  width: 55%;
  z-index: -1;
}

.cards-wrapper .card-content {
  color: var(--clr-neutral-100);
  display: flex;
  flex-direction: column;
  gap: 1rem;
  inset: 0 6.63rem;
  position: absolute;
  padding-block: var(--size-800);
}

.cards-wrapper .card-content h1 {
  font-family: var(--ff-heading);
  font-size: var(--fs-primary-heading);
  font-weight: var(--fw-bold);
  letter-spacing: 0.5rem;
}

.cards-wrapper .card-content h3 {
  color: var(--clr-neutral-100);
  font-weight: var(--fw-semi-bold);
}

.cards-wrapper .card-content a {
  align-items: center;
  color: var(--clr-neutral-100);
  cursor: pointer;
  display: flex;
  font-family: var(--ff-primary);
  font-size: var(--fs-body);
  font-weight: var(--fw-bold);
  letter-spacing: 0.16rem;
}

.cards-wrapper .card-content i {
  color: var(--clr-neutral-100);
  cursor: pointer;
  font-size: 5rem;
  margin-inline: 1.3rem;
  margin-block: 3rem;
}

/* contact section */

.container-contact {
  background: pink;
  text-align: center;
  margin-inline: auto;
  position: relative;
  height: 38.875rem;
  width: 102.625rem;
}

.container-contact::before {
  background: var(--clr-primary-700);
  content: "";
  clip-path: polygon(0 0, 100% 0, 70% 100%, 0% 100%);
  top: -5rem;
  left: -5rem;
  position: absolute;
  width: 34.34375rem;
  height: 40.1875rem;
  z-index: -1;
}

.container-contact h1 {
  font-family: var(--ff-secondary);
  font-size: var(--fs-primary-heading);
  background: -webkit-linear-gradient(#014fa1, #0568bf);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.container-contact h3 {
  color: var(--clr-primary-700);
  font-size: var(--fs-600);
  font-weight: var(--fw-bold);
  letter-spacing: 0.24rem;
  margin-bottom: 2.56rem;
}

.container-contact .cols-wrapper {
  display: flex;
  gap: 5rem;
  justify-content: center;
}

.container-contact .cols-wrapper .col {
  align-items: center;
  background-color: var(--clr-primary-800);
  color: var(--clr-neutral-100);
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 17.375rem;
  width: 38.75rem;
}

.container-contact .cols-wrapper .col h5 {
  font-family: var(--ff-primary);
  letter-spacing: 0.1125rem;
  margin-bottom: var(--size-650);
}

.container-contact .cols-wrapper .col p {
  font-family: var(--ff-secondary);
  letter-spacing: 0.2rem;
}

.container-contact .cols-wrapper .col p span {
  font-weight: var(--fw-semi-bold);
  font-size: var(--fs-750);
}

.container-contact .cols-wrapper .col .btn {
  border: 0.063rem solid var(--clr-neutral-100);
  border-radius: 2.5rem;
  background: transparent;
  color: var(--clr-neutral-100);
  font-size: var(--size-400);
  padding-block: 1.25rem;
  padding-inline: 4.2rem;
}

.container-contact .cols-wrapper .col .btn:focus {
  border-color: none;
}
