@use "./../foundation/system" as s;

:root {
  /* COLOR */

  /* <-- primary-color --> */
  --clr-primary-200: rgba(255, 255, 255, 0.85); /* <-- Header */
  --clr-primary-400: #0568bf; /* <-- 400 set as standard */
  --clr-primary-500: #0567bd; /* <-- Big circle bottom & hero heading bar*/
  --clr-primary-600: #014fa1; /* <-- xsm icon arrow */
  --clr-primary-700: #003d7c; /* <-- Contact col sub-title */
  --clr-primary-800: rgba(1, 46, 93, 0.9); /* <-- Contact col */

  /* <-- primary-grandient-color --> */

  --clr-gradient-400: linear-gradient(#014fa1, #0568bf);
  --clr-gradient-500: linear-gradient(#014fa1, #0462b8, #0568bf);
  --clr-gradient-600: linear-gradient(#012142, #0568bf);
  --clr-gradient-800: linear-gradient(#030303, #003d7c);

  /* <-- neutral-color --> */
  --clr-neutral-100: #fff; /* Light color */
  --clr-neutral-200: #f2f4f7; /* <-- Form bg */
  --clr-neutral-300: #f3f3f3; /* <-- Hero heading bar*/
  --clr-neutral-600: #333; /* <-- Primary body text */
  --clr-neutral-900: #000; /* Dark color */

  /* FONT FAMILY */
  --ff-primary: "Gothic A1", sant-serif; /* <-- 400, 600, 700 | Yu Gothic Not yet downloaded*/
  --ff-secondary: "Oswald", sant-serif; /* <-- 600, 700*/
  --ff-tertiary: "Inter", sant-serif; /* <-- 700 */
  --ff-minor: "DIN Condensed", sant-serif; /* <-- Need it?? Not yet downloaded*/

  /* Re-custom properties here*/
  --ff-body: var(--ff-primary);
  --ff-heading: var(--ff-secondary);

  /* FONT WEIGHT */
  --fw-regular: 400;
  --fw-semi-bold: 600;
  --fw-bold: 700;

  /* FONT SIZE */
  --fs-400: 1rem; /* Set as a regular size <p>*/
  --fs-500: 1.125rem; /* 18px <h5>*/
  --fs-550: 1.25rem; /* 20px <h4>*/
  --fs-600: 1.5rem; /* 24px <h3>*/
  --fs-700: 2rem; /* 32px <h2>*/
  --fs-750: 2.25rem; /* 36px */
  --fs-800: 4rem; /* 64px Numeric bullet point*/
  --fs-850: 6rem; /* 96px Hero*/
  --fs-900: 6.25rem; /* 100px <h1>*/

  /* Re-custom properties here*/
  --fs-primary-heading: var(--fs-900);
  --fs-secondary-heading: var(--fs-700);
  --fs-body: var(--fs-400);
  --fs-nav: var(--fs-500);
  --fs-button: var(--fs-500);

  /* SIZE */
  --size-100: 0.25rem; /* 4px */
  --size-200: 0.5rem; /* 8px */
  --size-300: 0.75rem; /* 12px */
  --size-400: 1rem; /* 16px */
  --size-450: 1.5rem; /* 24px */
  --size-500: 1.75rem; /* 28px */
  --size-650: 2rem; /* 32px */
  --size-600: 3rem; /* 48px */
  --size-700: 4rem; /* 64px */
  --size-800: 6rem; /* 96px */
  --size-850: 7.5rem; /* 120px */
  --size-900: 10rem; /* 160px */
}

/* --- GENERAL STYLING --- */
/* header section */

html {
  max-width: 100vw;
  padding: 0;
  padding-top: 0.5rem;
}

a {
  cursor: pointer;
  text-decoration: none;
}

header {
  background: var(--clr-primary-200);
  padding: 0;
}

header::after {
  background-color: pink;
  content: "";
  height: 100vh;
  position: absolute;
  width: 100%;
  // background-image: url("/src/asset/img/ship.jpg");
  // background-repeat: no-repeat;
  // background-size: cover;
  // background-position: center;
  top: 0;
  right: 20;
  z-index: -1;
}

header .nav-wrapper {
  align-items: center;
  display: flex;
  font-weight: var(--fw-bold);
  justify-content: space-between;
}

header .nav-wrapper .logo {
  color: var(--clr-primary-400);
  position: relative;
  margin-right: auto;
  margin-left: var(--size-600);
}

header .logo::before {
  background-color: var(--clr-primary-400);
  content: " ";
  display: block;
  position: absolute;
  height: 0.5rem;
  width: 41rem;
  top: -85%;
  left: 0;
}

header .nav-wrapper .nav-links {
  align-items: center;
  padding: 0;
  margin: 0;
  margin-right: 1rem;
  display: flex;
  // overflow-x: hidden;
}

header .nav-wrapper .nav-links li {
  border-bottom: solid 2px transparent;

  cursor: pointer;
  margin: var(--size-450) var(--size-650);
  position: relative;
}

header .nav-wrapper .nav-links li::after {
  border-bottom: solid 3px transparent;
  content: "";
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: -20px;
  transition: transform 0.6s linear;
  transform: scale(0.97, 0.93);
  z-index: 2;
}

header .nav-wrapper .nav-links li:hover::after {
  border-bottom: solid 3px #0567bd;
  transition: transform 0.6s linear;
  transform: scale(0.97, 0.93);
}

header .nav-wrapper .nav-links li a {
  color: var(--clr-neutral-600);
  font-size: var(--fs-500);
}

header .nav-wrapper .paralleragon {
  background: var(--clr-primary-400);
  font-size: var(--fs-500);

  text-align: center;
  clip-path: polygon(15% 0%, 100% 0%, 85% 100%, 0% 100%);
  padding-block: 2rem;
  margin-right: -2.8rem;
  height: 100%;
  width: 18rem;
}

header .nav-wrapper .paralleragon a {
  color: var(--clr-neutral-100);
  font-weight: var(--fw-bold);
}

header .nav-wrapper .paralleragon a i {
  font-size: var(--fs-500);
  margin-right: 0.5rem;
}

header .nav-wrapper .trapezium {
  background: var(--clr-neutral-900);
  clip-path: polygon(32% 0, 100% 0%, 100% 100%, 0% 100%);
  padding-left: 2.8rem;
  width: 8.5rem;
  height: 5.8rem;
}

header .nav-wrapper .trapezium a {
  background: var(--clr-neutral-900);
  color: var(--clr-neutral-100);
  display: flex;
  font-size: var(--fs-600);
}

header .nav-wrapper .trapezium a i {
  font-size: var(--fs-700);
  padding-top: 1.8rem;
  padding-left: 1rem;
}
